.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fader {
  top: 0px;
  left: 0px;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #282c34;
  z-index: 3;
  opacity: 70%;
}

.panel {
  background-color: white;
  border-radius: 30px;
  box-shadow: 30px 30px #1C1C24;
  margin: auto;
  padding: 20px;
}

.disc {
  display: flex;
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.slot {
  display: flex;
  width: 100px;
  height: 100px;
}

.button-holder {
  display: flex;
  justify-content: flex-end;
  flex-flow: column;
  height: 70%;
}

.button {
  border-radius: 30px;
  background-color: #282c34;
  padding: 10px;
  color: white;
}

.button:hover {
  background-color: #1C1C24;
  cursor: pointer;
}